import React from 'react';
import Header from './components/layout/Header';
import { BrowserRouter as HashRouter, Route, Switch } from 'react-router-dom';
import About from './components/pages/about';
import Home from './components/pages/home';
// import Contact from './components/pages/contact';
import News from './components/pages/news';
import  ScrollButton  from "./components/ScrollToTop";
import Rosters  from "./components/pages/rosters";
import meetCLoL from "./components/pages/posts/meetCLoL";
import meetOWred from './components/pages/posts/meetOWred';

import './App.css';



function App() {
  return (
	<HashRouter basename="/esportswebite/">	
		<Header />
		<ScrollButton/>
		<main style={{backgroundColor: 'lightgray'}}>
			<div className='container'>
				<Switch>
					<Route exact path='/esportswebsite/' component={Home}/>
					<Route path='/esportswebsite/about' component={About}/>
					{/* <Route path='/contact' component={Contact}/> */}
					<Route path='/esportswebsite/news/meetCLoL' component={meetCLoL} />
					<Route path='/esportswebsite/news/meetOWred' component={meetOWred} />
					<Route path='/esportswebsite/news' component={News} />
					<Route path='/esportswebsite/roster' component={Rosters} />
				</Switch>
			</div>
			
		</main>
		
	</HashRouter>
  );
}

export default App;
